.btnWrap {
  margin-bottom: 10px;
}
.table tr > td,
.table tr > th {
  vertical-align: middle;
}
.table tr > td.img,
.table tr > th.img {
  width: 100px;
}
.table tr > td img,
.table tr > th img {
  max-height: 100px;
}
.table tr > td .btn,
.table tr > th .btn {
  margin-right: 5px;
}
.table tr > td .btn:last-of-type,
.table tr > th .btn:last-of-type {
  margin-right: 0;
}
.table tr > td.bold,
.table tr > th.bold {
  font-weight: bold;
}
.table tr > td:nth-child(1),
.table tr > th:nth-child(1),
.table tr > td:nth-child(2),
.table tr > th:nth-child(2) {
  width: 40px;
  text-align: center;
}


