.table.fixed-table > thead {
  display: block;
  position: relative;
}
.table.fixed-table > tbody {
  display: block;
  overflow: auto;
  height: 500px;
}
.table.fixed-table tr > th,
.table.fixed-table tr > td {
  min-width: 100px;
}
.table > tbody > tr > th {
  width: 100px;
}
.table > tbody > tr > td p {
  margin: 0;
}


